.Contato{
    width: 100%;
    padding: 10rem 0;
    background-image: url(../assets/shinehero.svg);
    background-repeat: no-repeat;
    background-position: 10% -10%;
}
.ContContainer{
    width: 65%;
    margin: 0 auto;
}
.ContTitulo span{
    color: var(--dark3);
}
.ContTitulo hr{
    margin: 5rem 0;
    height: 1px;
    border-color: var(--dark3);
}
.ContContainer img{
    height: auto;
    width: 100%;
    border-radius: 16px;
}